<template>
  <div class="card mt-3">
      <div class="card-body">
          <div class="card-title">
              <h3>{{this.$route.query.title}}</h3>

              <hr>
          </div>
          <div class="card-body">
             <div class="messages" v-for="(msg, index) in messages" :key="index">
                  <p><span class="font-weight-bold">{{ msg.user }}: </span>{{ msg.message }}</p>
              </div>
          </div>
      </div>
      <div class="card-footer">
          <form @submit.prevent="sendMessage">
              <div class="gorm-group">
                  <label for="user">이름:</label>
                  <input type="text" v-model="user" class="form-control">
              </div>
              <div class="gorm-group pb-3">
                  <label for="message">메시지:</label>
                  <input type="text" v-model="message" class="form-control">
              </div>
              <v-btn @click="sendMessage">보내기</v-btn>
          </form>
             <v-btn @click="leave">나가기</v-btn>
      </div>
  </div>
</template>
<script>
import io from 'socket.io-client'


export default {
    data () {
        return {
            user: '',
            message : '',
            messages : [],
            socket : io('localhost:3000')
           
        }
    },
    methods : {
        sendMessage () {
       
            this.socket.emit('chat message', { 
                        user : this.user,
                        message : this.message,
                        roomname : this.$route.query.title
                        })
            this.message = ''
        },
        leave () {
            this.socket.emit('leaveRoom',this.$route.query.title)
            this.$router.replace({path : '/'})
        }
    },
    created() {
         
    },
    mounted () {
        this.socket.on('chat message', (msg) => {
            this.messages = [...this.messages, msg];
        })
        this.socket.emit('joinRoom', this.$route.query.title) 
    }


}
</script>